	import uniCountdown from "@/components/uni-countdown/uni-countdown.vue"

	export default {
		data() {
			return {
				style4:"",
				desc:"",   //退货描述
				array: [{id:0,name:"我不想买了"},{id:1,name:"信息填写错误"},{id:2,name:"重新拍"},
				{id:3,name:"卖家缺货"},{id:4,name:"其他原因"}],  
				
				show_menu: false,
				order_no:'',
				down:true,
				hour:"",
				minute:"",
				miao:"",
				string:"",
				order:{},
				status:["已取消","等待买家付款","等待买家付款","订单待发货","订单待收货","订单已完成"],
				status2:["已取消","剩12小时50分钟自动关闭","剩12小时50分钟自动关闭","请耐心等候哦~","宝贝正在努力的向您奔跑~","欢迎您的再次光临！"],
				goodsList: [
					
				],
				datatime:"",
			}
		},
		components: {uniCountdown},
		onLoad(e){
			
			this.order_no = e.id;
			console.log(this.order_no)
			this.request({
				url:this.config.url+'order/detail',
				method: 'POST',
				data: {
					order_no:this.order_no,
					token:this.token
				},
				success: res => {
					console.log("res",res);
					if(res.data.code == 1){
						this.goodsList = res.data.data.order_list;
						this.order = res.data.data;
						console.log("order",this.order)
						let datatime=res.data.data.create_at;
						this.getDistanceTime(datatime);
						setInterval(()=>this.getDistanceTime(datatime),1000);
						if(this.order.status == 2){
							this.down = false;
						}
					}
				},
			});
			
		},
		methods: {
			
			getDistanceTime(time){
				let _this=this;
				var endTime= new Date(Date.parse(time.replace(/-/g, "/")));/*replace将时间字符串中所有的'-'替换成'/',parse将时间格式的字符串转换成毫秒*/
				var nowTime = new Date();
				let m = 60*1000*60*24;
				var distance =endTime.getTime()+ m - nowTime.getTime();/*getTime把一个date对象转换成毫秒*/

				var day = 0;
				var hour = 0;
				var minute = 0;
				var second = 0;

				if(distance >= 0){
					day = Math.floor(distance/1000/60/60/24);
					hour = Math.floor(distance/1000/60/60%24);
					minute = Math.floor(distance/1000/60%60);
					second = Math.floor(distance/1000%60);
				}else{
					_this.string="已取消"
				}

				_this.string =  hour + "时" + minute + "分" + second + "秒";
			},
			//查看物流
			viewSend() {
				console.log(this.order_no)
				var _this=this;
				this.request({
					url: this.config.url + "order/express",
					method: "POST",
					data: {
						
					order_no: this.order_no,
					},
					success: res => {
						console.log(res)
						if(res.data.code==1){
							var goods=JSON.stringify(this.order)
							var info=JSON.stringify(res.data.data)
							uni.navigateTo({
								url:"/pages/viewsend/viewsend?info="+info+"&goods="+goods
							})
						}else{
							uni.showToast({
								title:res.data.info
							})
						}
					}
				})
			},
			//申请售后
			service() {
				uni.navigateTo({
					url: "/pages/serType/serType"
				})
				uni.setStorage({
					key: "regoods",
					data: this.order,
					success: () => {
						console.log('保存换货数据成功')
					}
				})
			},
			//取消订单
			cancelOrder(){
				
				 this.show_menu = true;
				 this.num=index;
				 this.order=order;
			},
			// 退货取消
			cancel(){
				this.show_menu=false
			},
			//确认
			sure(){
				if(this.desc){
					
					this.show_menu=false
					this.request({
						url:this.config.url+"order/cancle",
						method:"POST",
						data:{
							token:this.token,
							order_no:this.order_no,
							cancle_desc:this.desc
						},
						success:res =>{
							console.log(res)
							
							if(res.data.code==1){
								uni.showToast({
									title:"取消订单成功"
								})
								uni.navigateTo({
									url:"/pages/user/order_list/order_list?tbIndex="+1
								})
								
							}else{
								uni.showToast({
									title:"取消订单失败"
								})
							}
						}
					})
				}else{
					uni.showToast({
						title:"请选择退货原因",
						icon:'none'
					})
					
				}
			},
			//确认收货
			confirm(){
				
				this.request({
					url:this.config.url+"order/confirm",
					data:{
						order_no:this.order.order_no
					},
					method:"post",
					success:(res)=> {
					
						if(res.data.code==1){
							var con=JSON.stringify(this.order)
							console.log( "确认收货",res)
							uni.navigateTo({
								url:"/pages/confirm/confirm?con="+con
							})
						}
					}
				})
			},
				//删除订单
			deleteOrder() {
				console.log(this.order_no)
				this.request({
					url: this.config.url + "order/del",
					method: "POST",
					data: {
						token: this.token,
						order_no: this.order_no,
					},
					success: res => {
						console.log(res)
						if (res.data.code == 1) {
							uni.showToast({
								title: "删除订单成功"
							})
							uni.navigateBack({
								
							})
						}
					}
				})
			},
			//跳转订单
			evalute(){
				var goods=JSON.stringify(this.order)
				uni.navigateTo({
					url:"/pages/user/keep/sayFeel/sayFeel?eval="+goods
				})
				
			},
            showcityfour(id,name){
           	this.style4=id;
           	this.desc=name;
           },
		   //去付款
		   toPayment() {
		   	//调起支付接口
		   	var _this = this;
		   	this.request({
		   		url: this.config.url + "order/pay",
		   		method: "POST",
		   		data: {
		   			token: this.token,
		   			order_no: this.order_no
		   		},
		   		success: (res) => {
		   			console.log(res)
		   			if (res.data.code == 1) {
		   				var pay = res.data.data.data
		   				this.requestPayment({
		   					provider: 'wxpay',
		   					appid: pay.appId,
		   					timeStamp: pay.timeStamp,
		   					nonceStr: pay.nonceStr,
		   					package: pay.package,
		   					signType: pay.signType,
		   					paySign: pay.paySign,
		   					success: function(res) {
		   						console.log('success:' + JSON.stringify(res));
							
		   						
		   						uni.showToast({
		   							title: "支付成功",
		   
		   						})
									//跳转我的订单
								uni.navigateTo({
									url:"/pages/user/order_list/order_list?tbIndex="+1
								})
		   					},
		   					fail: function(err) {
		   						//console.log('fail:' + JSON.stringify(err));
		   						uni.showToast({
		   							title: "支付失败"
		   						})
		   					}
		   				})
		   			} else {
		   				uni.showToast({
		   					title: res.data.info
		   				})
		   			}
		   		}
		   	})
		   
		   },
		}
		
	}
